<!-- wzh -->
<template>
  <div>
    <detailMoreIntroduction ref="detailM" :isAlert="detailData.description" v-on:pressDown="keepDo"></detailMoreIntroduction>
    <div class="detail-top">
      <div class="player-window" :class="{'active':position===0}">
        <div class="Bg"  v-show="!ispayss"></div>
        <VideoPlayer v-if="isShowPlay && ispayplay" v-bind:detailData="detailData" v-bind:currShow="currShow"
        ref="videoplayer"
        v-bind:psId="psId"
        v-bind:playerCore="playerCore"
        v-bind:isFullScreen="false"
        v-bind:isFree="isFree"
        v-bind:isBought="isBought"
        v-bind:lastFocusId="lastFocusId"
        :size="size"
        :shouye="shouye"
        :name="name"
        :fee ="fee"
        v-on:initFocus="initFocus($event)"
        v-on:setCurrPlay="setCurrPlay($event)"
        v-on:goCloudPay="goCloudPay($event)"
        v-on:onPlayStart="onPlayStart($event)"
        v-on:onPlayError="showError(3)"
        v-on:onPlayOver="onPlayOver()">
        >
        </VideoPlayer>
      </div>
      <div class="details">
        <div class="text-box">
          <p>{{detailData.assetName}}</p>
          <p>{{detailData.area}} | {{detailData.year}} | {{detailData.language}}</p>

        </div>
        <div class="introduction">
          <div class="word"><p class="text">{{detailData.description}}</p></div>
          <span :class="{'active':position === 4}"></span>
        </div>
        <div class="btn_content">
            <div class="full" :class="{'full_active':position===1}"></div>
          <div class="collection" :class="{'active':position===2,'noCollect':noCollect,'isCollect':!noCollect}"></div>
          <div :class="{'orderBtn':true}" v-if="!isMianfei">
            <div :class="{'orderBtn_hover':position === 3}"></div>
          </div>
          </div>
      </div>
    </div>
    <div class="markList">
       <ul ref="marks">
         <li v-for="(item,index) in detailData.itemList" :key="index" :id="('mark-'+index)" :class="">
           <i v-if="index==0 && !isBought"></i>
         </li>
      </ul>
    </div>
    <div class="detail-content">
      <ul ref="contentBox">
        <li v-for="(item,index) in detailData.itemList" :key="index" :id="('box-'+index)" :class="{'active':index===itemNo && !isTop && isShow}">
          <span :class="{'isplay':index===isplay}">{{item.itemName}}</span>
        </li>
      </ul>
    </div>
    <keyDo ref="keydo" v-on:listenKeyCode="keyCode" v-if="isShow"></keyDo>
    <!-- <div v-show="isPay" id="pay"></div> -->
    <div v-show="isPay" id="pay">
      <div :class="{'active':isGoingPayJump =='left'}"></div>
      <div :class="{'active':isGoingPayJump =='right'}"></div>
      <p class="pay_text"></p>
      <p class="pay_textCenter">
</p>
    </div>
    <div v-show="isFree" class="trySee">
      <p class="trySeeText">5   </p>
    </div>
  </div>
</template>

