<script>
import {
  HTTPUtil as api
} from '../../fetch/api.js';
import {
  mapGetters
} from 'vuex';
import {
  client as yh
} from '../../js/client.js'
import detailMoreIntroduction from './detail-more-introduction.vue'
import c from '../../js/common.js';
import { yst } from '../../js/yst.js';
import VideoPlayer from '../play/VideoPlayer'
import PlayCore from '../play/playerCode.js'
import DetailRecordHelper from '../play/DetailRecordHelper'
import {bi} from '../../js/logApi.js'
import { auth } from '../../js/auth'
import Toast from '../../tool/toast/toast';

export default {
  props: ['initData', 'jsonUrl'],
  data () {
    return {
      name: 'detail_top',
      detailData: [],
      isShow: true,
      isTop: true,
      position: 0,
      itemNo: -1,
      lastFocus: 0,
      topLastFocus: 0,
      isplay: 0,
      noCollect: true,
      size: {x: 60, y: 90, width: 480, height: 270},
      playerCore: PlayCore.getPlayCore(),
      isPay: false,
      shouye: 'detop',
      currShow: 0, // 0:  1 2 3
      // view
      lastFocusId: '',
      classifyDirection: '',
      isFree: false,
      isBought: false,
      psId: '',
      startTime: 0,
      cloudPayUrl: '',
      ispayss:true,
      ispayplay: false,
      isMianfei: true,
      initDataTime: -1,
      json: '',
      trySee: true,
      fee: '',
      point: '',
      isGoingPayJump:'left' //
    }
  },
  created () {
    this.orderMsgBack();
  },
  mounted () {
    this.startTime = new Date() - 0;
  },
  components: {VideoPlayer, detailMoreIntroduction},
  computed: {
    ...mapGetters(['behindParams', 'episode', 'smallScreenState', 'navpos', 'isShowPlay', 'authStatus', 'isBack'])
  },
  watch: {
    ispayplay (){
    },
    isBack (){
      // if (this.itemNo > 0) {
        if (this.isBack == false){
          clearTimeout(acc);
          let acc = setTimeout(() => {
            this.$store.commit('GET_ISBACK', true);
          }, 1000);
        }
      // }
    },
    // isMianfei(){
    //   if (this.isMianfei==false) {
    //       clearTimeout(acc);
    //       let acc = setTimeout(()=>{
    //         this.$store.commit('GET_ISBACK', true);
    //       },3000);
    //   }
    // },
    initData () {
      this.detailData = this.initData;
      if (this.jsonUrl) {
        localStorage.setItem('historyUrl', this.jsonUrl)
        this.json = this.jsonUrl;
      } else {
        this.json = localStorage.getItem('historyUrl')
      }
      // this.json = {};
      this.$store.commit('GET_ISBACK', false);
      this.$store.commit('GET_ISSHOWPLAY', true);
      // 
       if (localStorage.getItem("payzero")) {
          if(!this.isBought){
            this.isPay = true;
            this.ispayplay = false;
            this.ispayss = false;
            this.$store.commit('GET_ISBACK', true);
          }
        }
      
      if (!this.detailData.packageId){
        this.isMianfei = true;
      } else {
        this.isMianfei = false;
      }
      try {
        this.collectData(); // 
        this.queryHistory();
        if (yh.playHistory(this.detailData.assetId) == ''){
          this.isplay = 0;
          this.itemNo = 0;
          this.$refs.contentBox.style.marginLeft = '35px';
          this.$refs.marks.style.marginLeft = '35px';
        } else {
          // let json = JSON.parse(yh.playHistory(this.detailData.assetId));
        }
      } catch (error){
        console.log(error)
      }
      this.$nextTick(function () {
        this.logUp();
        let self = this;
        // self.$store.commit('GET_ISBACK', true);
        auth.queryBoughtCp(
          //
          function (data) {
            let jsonData = data;
            if (jsonData.result === 400 || jsonData.result === '400') {
              self.cloudPayUrl = jsonData.cloudPayUrl; // 
              console.log('' + self.cloudPayUrl);
              self.$store.commit('GET_AUTHSTATUS', false);
              // self.$store.commit('GET_ISBACK', true);
              self.$store.commit('GET_EPISODE', 0);
              self.isBought = false;
              self.isMianfei = false;
              if (self.detailData.itemList.length == 1) {
                if (!self.isBought){
                  self.isFree = true;
                }
              }
            } else if (jsonData.result === 0 || jsonData.result === '0') {
              console.log('');
              yh.vip = '1';
              self.$store.commit('GET_AUTHSTATUS', true);
              self.isBought = true;
              self.isMianfei = true;
            } else if (jsonData.result === 340 || jsonData.result === '340'){
              self.$store.commit('GET_AUTHSTATUS', true);
              console.log('' + JSON.stringify(jsonData));
              self.isBought = true;
              self.isMianfei = true;
            }
            self.isplaynum(self.episode);
            self.itemNo = self.isplay;
            self.ispayplays();
          },
          this.initData.assetId,
          this.initData.packageId
        );

        // this.ispayplays();
        // 1200127490this.initData.assetId
      });
    }
  },
  methods: {
    ispayplays (){
      if (this.authStatus == true || this.isMianfei == true){
        this.ispayplay = true;
        this.ispayss = true;
        this.fee = 1;
      } else {
        if (this.itemNo == 0){
          if (localStorage.getItem('payzero')) {
            localStorage.removeItem('payzero');
            if (!this.isBought){
              this.isPay = true;
              this.ispayplay = false;
              this.ispayss = false;
            }
          } else {
            this.ispayplay = true;
            this.ispayss = true;
          }
          this.fee = 3;
        } else {
          this.ispayplay = false;
          this.ispayss = false;
          this.fee = 2;
        }
      }
    },
    queryHistory (){
      let jsonob = '';
      try {
        jsonob = DetailRecordHelper.getRecord(this.detailData.assetId);
        try {
          if (typeof (jsonob) === 'string'){
            jsonob = JSON.parse(jsonob);
          }
        } catch (error) {
          jsonob = '';
        }
        if (jsonob == ''){
          console.log('');
          this.$store.commit('GET_EPISODE', 0);
        } else {
          this.$store.commit('GET_EPISODE', jsonob.currentEpisode);
        }
      } catch (error) {

      }
    },
    isplaynum (num){
      // 
      console.log('' + num);
      this.isplay = num * 1;
      this.lastFocus = num * 1;
      if (this.isplay >= 4){
        this.$refs.contentBox.style.marginLeft = 35 - (this.isplay - 3) * 251 + 'px'
        this.$refs.marks.style.marginLeft = 35 - (this.isplay - 3) * 251 + 'px'
      }
    },
    playnumenter (){
      // 
      this.isplay = this.itemNo;
      if (this.isplay >= 4){
        this.$refs.contentBox.style.marginLeft = 35 - (this.isplay - 3) * 251 + 'px'
        this.$refs.marks.style.marginLeft = 35 - (this.isplay - 3) * 251 + 'px'
      }
    },
    collectData () {
      console.log('');
      let time = new Date().getTime();
      let url =
          'http://112.18.251.100:7077/uds/cloud/collection/list?version=1&siteId=' + yh.siteId + '&userId=' + yh.userId +
          '&relateId=' + this.initData.assetId + '&collectType=1' + this.behindParams + '&time=' + time
      api.get(url, (res) => {
        // console.log(res);
        if (res.data.resultNum === 1) {
          this.noCollect = false
        } else {
          this.noCollect = true
        }
      })
    },
    addCollect () {
      let url = 'http://112.18.251.100:7077/uds/cloud/collection/collect?version=1';
      let id = this.detailData.assetId;
      let data = {
        siteId: yh.siteId,
        userId: yh.userId,
        collectType: 1, // (0-,1-,2-)
        relateId: id,
        relateTitle: this.detailData.assetName,
        relateScore: this.detailData.score,
        relateImg: this.detailData.assetImgH,
        relateUrl: this.jsonUrl,
        relateLayout: this.detailData.layout
      };
      let jsonOb = {
        click_type: '1',
        cid: id,
        collect: '1'
      }
      bi.collection(jsonOb);
      api.post(url, data, (res) => {
        console.log(res);
        if (res.mes === 'ok') {console.log('');}
      })
    },
    delCollect () {
      let url = 'http://112.18.251.100:7077/uds/cloud/collection/del?version=1';
      let id = this.detailData.assetId;
      let data = {
        siteId: yh.siteId,
        userId: yh.userId,
        collectType: 1, // (0-,1-,2-)
        relateId: id,
        layout: this.detailData.layout
      }
      
      try{
            let jsonOb = {
              click_type: '1',
              cid: id,
              collect: '2'
            }
            bi.collection(jsonOb);
          }catch(error){
            console.log(error);
          }
      api.post(url, data, (res) => {
        console.log(res);
        if (res.mes === 'ok') { console.log(''); }
      })
    },
    keyCode (kc) {
      if (!this.detailData){
        if (kc == 'KeyBack'){
          // this.setLogData();
          if (this.$route.params.bp && this.$route.params.bp.type){
            c.routerBack(this.$router, this.$route.params.bp.type)
          } else {
            c.routerBack(this.$router, undefined)
          }
        }
      } else {
        if (!this.isBack) return;
        if (kc === 'down') {
          this.down();
        } else if (kc === 'up') {
          this.up();
        } else if (kc === 'left') {
          this.left();
        } else if (kc === 'right') {
          this.right();
        } else if (kc === 'KeyEnter') {
          this.enter();
        } else if (kc === 'KeyBack') {
          if (this.isPay){
            this.isPay = false;
            clearTimeout(this.initDataTime);
            this.initDataTime = -1;
            // this.shouye = "shouye";
          } else {
            // this.setLogData();
            this.$store.commit('GET_EPISODE', 0);
            this.$store.commit('GET_ISSHOWPLAY', false);
            let backTypeUrl = localStorage.getItem('backType')
            if (backTypeUrl){
              localStorage.removeItem('backType');
            }
            if (this.$route.params.bp && this.$route.params.bp.type || backTypeUrl){
              if (this.$route.params.bp && this.$route.params.bp.type){
                c.routerBack(this.$router, this.$route.params.bp.type);
              } else {
                c.routerBack(this.$router, backTypeUrl);
              }
            } else {
              c.routerBack(this.$router, undefined);
            }
          }
        }
      }
    },
    left () {
      if (this.isPay) {
        this.isGoingPayJump = 'left';
      }else{
        if (this.isTop) {
        if (this.position === 0 || this.position === 4) return
        this.position--
        } else {
          if (this.itemNo === 0) return
          if (this.itemNo >= 3) {
            this.$refs.contentBox.style.marginLeft = 35 - (this.itemNo - 3) * 251 + 'px'
            this.$refs.marks.style.marginLeft = 35 - (this.itemNo - 3) * 251 + 'px'
          }
          this.itemNo--
        }
      }
    },
    right () {
      if (this.isPay) {
        this.isGoingPayJump = 'right';
      }else{
        if (this.isTop) {
        if (this.position === 3 || this.position === 4) return
        if (this.isMianfei && this.position === 2) {
          return
        }
        this.position++
        } else {
        if (this.itemNo === this.detailData.itemList.length - 1) return
        this.itemNo++
        if (this.itemNo >= 4) {
          this.$refs.contentBox.style.marginLeft = 35 - (this.itemNo - 3) * 251 + 'px'
          this.$refs.marks.style.marginLeft = 35 - (this.itemNo - 3) * 251 + 'px'
        }
      }
      }
    },
    up () {
       if (this.isPay) {
        return
      }
      if (this.isTop) {
        if (this.position == 1 || this.position == 3 || this.position == 2){
          this.position = 4;
        }
        return;
      }
      this.isTop = true
      this.position = this.topLastFocus
      this.lastFocus = this.itemNo
      this.itemNo = -1
    },
    down () {
      if (this.isPay) {
        return
      }
      if (this.isTop) {
        if (this.position == 4){
          if (this.isMianfei) {
            this.position = 2;
          } else {
            this.position = 3;
          }
          return;
        }
        this.isTop = false
        this.itemNo = this.lastFocus
        this.topLastFocus = this.position
        this.position = -1
      } else {
        this.isShow = false
        this.lastFocus = this.itemNo
        this.itemNo = -1
        this.$emit('pressDown', this.isShow, this.detailData, 'down');
      }
    },
    enter () {
      if (this.isPay) {
        DetailRecordHelper.recordSchedule(this.detailData.assetId, 0, 0);
        if (this.isGoingPayJump == 'left') {
          this.isPay = false;
          this.isGoingPayJump = 'left';
          this.ispayplay = true;
          this.ispayss = true;
        } else {
          this.orderingJump();
        }
      } else {
        if (this.isTop) {
        if (this.position === 0 || this.position === 1) {
          if (this.isBought || this.episode == 0) {
            let historyData = {
              bookMarkTime: new Date().getTime(), // 
              watchTime: '', // 
              collectType: 1, // (0-,1-,2-)
              relateId: this.detailData.assetId,
              relateTitle: this.detailData.assetName,
              relateScore: this.detailData.score,
              relateImg: this.detailData.assetImgH,
              layout: this.detailData.layout,
              jsonUrl: this.json
            };
            yh.addHistory(historyData);
            this.currShow = 3;
            let currentTime = 0;
            currentTime = this.$refs.videoplayer.getCurrentTime();
            this.$refs.videoplayer.destoryPlayer();
            console.log('' + currentTime);
            // DetailRecordHelper.recordSchedule(this.detailData.assetId, this.episode, currentTime);
            if (this.$route.params.bp && this.$route.params.bp.type){
              localStorage.setItem('backType', this.$route.params.bp.type);
            }
            c.setParentPageType('0301');
            c.setParentPageId(this.initData.assetId);
            this.$store.commit('GET_ISSHOWPLAY', false);
            this.$router.push({'name': 'play', params: {detailData: this.detailData, currShow: this.currShow, playerCore: this.playerCore, psId: this.psId, isFree: this.isFree, isBought: this.isBought, isFullScreen: true, shouye: this.shouye, fee: this.fee}})
          }
        } else if (this.position === 2) {
          if (this.noCollect) {
            this.addCollect()
          } else {
            this.delCollect()
          }
          this.noCollect = !this.noCollect
        } else if (this.position === 4) {
          this.isShow = false;
          this.$refs.detailM.getKeyListen(true);
        } else if (this.position === 3){
          if (this.jsonUrl){
            window.localStorage.setItem('detailJsonUrl', this.jsonUrl);
          }
          this.setLogData();
          // let biObj = {
          //   stay_time: new Date() - this.startTime,
          //   recmd_id: '',
          //   pos_id: null,
          //   page_type: '0301',
          //   page_id: this.initData.assetId,
          //   click_type: '108',
          //   click_id: '108-1'
          // }
          // bi.jump(biObj);
          if (this.authStatus){
            Toast('');
            return false;
          }
          try{
            let jsonOb = {
              page_id: this.initData.assetId,
              page_type: '0301'
            }
            bi.orderClick(jsonOb);
          }catch(error){
            console.log(error);
          }
          this.point = 1;
          this.orderingJump();
        }
      } else {// 

        if (this.fee == 1) {
          try{
            let logs = {
            asset_id: this.detailData.assetId,
            item_id: this.detailData.itemList[this.isplay].itemId,
            qb_datetime: this.startTime,
            zb_datetime: new Date().getTime(),
            duration: this.$refs.videoplayer.TotalTimel,
            ep: this.detailData.itemList.length,
            asset_type: this.detailData.fCategory,
            time: new Date().getTime() - this.startTime,
            error: this.$refs.videoplayer.errorMsg,
            fee: this.fee,
            parent_page_type: c.getParentPageType(),
            parent_page_id: c.getParentPageId(),
            pos_id: c.getPosition('columnId') || '',
            recmd_id: c.getRecmd_id() || '',
            isFullScreen: this.currShow == 3 ? 0 : 1
          }
          console.log('' + logs);
          bi.vod(logs);
          }catch(error){
            console.log(error);
          }
          
        }

        this.playnumenter();
        if (this.isBought) {
          alert(this.itemNo);
          this.$store.commit('GET_EPISODE', this.itemNo);
        }
        this.ispayplays();
      }
      }
    },
    getKeyListen (pos) { // 
      this.isShow = true
      if (pos == 'enter'){
        this.itemNo = 0;
      } else {
        this.itemNo = this.lastFocus
      }
    },
    keepDo (name, keyDo) { // 
      if (name === 'introduction') {
        this.isShow = true
      }
    },
    logUp () {
      try {
        let programInfo = {};
        programInfo.Program_name = this.detailData.assetName;
        programInfo.Program_id = this.detailData.assetId;
        programInfo.Product_name = '';
        programInfo.Product_code = '1018000004';
        programInfo.director = '';
        programInfo.actor = '';
        programInfo.year = this.detailData.year;
        programInfo.region = this.detailData.area;
        programInfo.label = '';
        programInfo.program_type = this.detailData.assetType;
        programInfo.program_status = 'ONLINE';
        programInfo.program_online_time = '2019-11-15 14:14:50';
        programInfo.program_offline_time = '2099-12-31 23:59:59';
        yst.contentInfo(programInfo);
      } catch (error) {
        console.log('');
        console.log(error);
      }
    },
    initFocus (force = false) {},
    setLogData () {
      c.setJump();
      c.setParentPageType('0301');
      c.setParentPageId(this.detailData.assetId);
    },
    setCurrPlay (index) {    
      this.$store.commit('GET_EPISODE', index);
      this.itemNo = this.episode;
      setTimeout(() => {
        if (this.episode == 0) {
          this.$refs.contentBox.style.marginLeft = 35 - (this.episode) * 251 + 'px'
          this.$refs.marks.style.marginLeft = 35 - (this.episode) * 251 + 'px'
        }
        this.isplaynum(this.episode);
      })
      // this.currPlayIndex = index;
    },
    goCloudPay (immediately = false, force = false) {
      console.log('goCloudPay isBought = ' + this.isBought + ' cloudPayUrl = ' + this.cloudPayUrl);
      // 
      if (!this.isBought){
        this.isPay = true;
        this.$store.commit('GET_ISBACK', false);
        this.ispayplay = false;
        this.ispayss = false;
        this.point = 3;
      }
    },
    onPlayStart (param) {
      console.log('' + param);
    },
    onPlayOver () {
      // if (this.currShow == 3)
      //     this.currShow = 0;
    },
    showError (errCode) {
      console.log('showError enter, errCode = ' + errCode);
      this.currShow = 0;
      let self = this;
      let params;
      // 1:   2: 3: 
      switch (errCode) {
        case 1:
          console.log('');
          break;

        case 2:
          console.log('');
          break;

        case 3:
          console.log('');
          break;
      }
    },
    orderingJump () {
      //    0123
      let orderingStrategyUrl = 'http://unionpay.sca.bcs.ottcn.com:8382/moviePackage2.3.sichuan.cp/main.html?custId=' +
        yh.userId + 'contentId=1&source=CP&productIds=1018000004&merchantId=SP_SJTV&businessType=VIDEO&action=danpian' + '&backUrl=' + location.href
      // api.jsonp(orderingStrategyUrl,(res) => {
      //   if(res.data && res.data.tacticType){
      //     localStorage.setItem('payWay',res.data.tacticType)
      //     if(res.data.tacticType == '1' || res.data.tacticType == '0'){
      //        this.$router.push({name: 'payfor',params:{url:this.cloudPayUrl}});
      //     }else if(res.data.tacticType == '2'){
      //       location.href = this.cloudPayUrl + '&backUrl=' + location.href;
      //     }
      //   }else{
      //     location.href = this.cloudPayUrl + '&backUrl=' + location.href;
      //   }
      // },function(){
      //   console.log('');
      //   location.href = this.cloudPayUrl + '&backUrl=' + location.href;
      // })
      let newurl = encodeURI(location.href);
      c.setParentPageType('0301');
      c.setParentPageId(this.detailData.assetId);
      c.setJump();
      // let urlss = this.cloudPayUrl+'&backUrl=' + newurl;
      // console.log("url"+urlss);
      this.$store.commit('GET_ISSHOWPLAY', false);
      this.$router.push({name: 'payfor', params: {detailData: this.detailData, movieUrl: newurl, point: this.point}});
    },
    orderMsgBack () {
      if (window.location.href.indexOf('?') !== -1){
        let str = window.location.href
        let urlArr = str.split('?')[1].split('&'); // &
        let urlObj = {};
        for (let i of urlArr) {
          urlObj[i.split('=')[0]] = i.split('=')[1]; // ===
        }
        
        try{
          var logOb = {
          pkg_type: '',
          pkg_id: '',
          pkg_price: '',
          action: '1',
          order_msg: urlObj.success,
          parent_page_id: c.getParentPageId(),
          parent_page_type: c.getParentPageType(),
          preview: 0
        };
        bi.order(logOb);
        }catch(error){
          console.log(error);
        }
      }
    }
  }
}
</script>



