auth.queryBoughtCp = function (callback, contentId, productIds = '') {
  let url = 'http://112.18.251.100:7077/inquiry?';
  // productIds = 's100000849';
  // // contentId = '1200127490';
  // let custId = 'songming2';
  var signMd5 = getCpSign(yh.userId, contentId, yh.zoneNo, productIds);
  // var signMd5 = getCpSign(custId, contentId, yh.zoneNo, productIds);
  url += 'custId=' + yh.userId + '&contentId=' + contentId + '&productIds=' + productIds + '&merchantId=' + yh.zoneNo + '&sign=' + signMd5;
  console.log('' + url);
  api.post(url, '', callback);
  console.log(1);
}

function getCpSign (custId, contentId, merchantId, productIds = '') {
  // console.log("getCpsign custId = " + custId + " contentId = " + contentId + " merchantId = " + merchantId);
  let param = []
  if (contentId != '') param.push('contentId' + '=' + contentId + '&')
  if (custId != '') param.push('custId' + '=' + custId + '&')
  if (merchantId != '') param.push('merchantId' + '=' + merchantId + '&')
  if (productIds != '') param.push('productIds' + '=' + productIds + '&')

  let result = ''

  for (let i = 0; i < param.length; i++) {
    result = result + param[i]
  }
  let privateKey = 'u4pkd1swo0inbq56tyxqdbjbgy4gc0ha'
  result += 'key=' + privateKey // todo privateKey
  result = result.toUpperCase()
  console.log('getCpSign result = ' + result)
  let md5 = MD5.hex_md5(result)
  console.log('getCpSign md5 = ' + md5)
  return md5
}
export {auth}



